import React, { createContext, useState, useCallback } from 'react';

const DashboardContext = createContext();
// eslint-disable-next-line react-hooks/exhaustive-deps
const VALUE_INIT_MATRICULADOS = [
    {
        Items: [],
    },
];
const VALUE_INIT_PROGRAMAS = [
    {
        Items: [],
        Chart: [],
        DonutData: [],
        Totales: [],
        InscritosAdmitidosMatriculados: [],
        ApexBarChartData: [],
        Piechart: [],
        EstadistInscritos: [],
        EstadistAdmitidos: [],
    },
];

const DashboardProvider = ({ children }) => {
    const [categoryGraduados, setStateGraduados] = useState(VALUE_INIT_PROGRAMAS[0]);
    const [categoryMatriculados, setStateMatriculados] = useState(VALUE_INIT_MATRICULADOS[0]);
    const [categoryAdmitidos, setStateAdmitidos] = useState(VALUE_INIT_MATRICULADOS[0]);
    const [categoryInscritos, setStateInscritos] = useState(VALUE_INIT_MATRICULADOS[0]);
    const [categoryPorFecha, setStatePorFecha] = useState(VALUE_INIT_MATRICULADOS[0]);
    const [isLoading, setLoading] = useState(true);
    const [jsonDataImport, JsonDataSetter] = useState('');
    const [selectedFile, setSelectedFile] = useState('');
    const [info, setInfo] = useState('');
    const [itemsmenu, setitemsMenu] = useState('');
    const [itemsmenuprincipal, setitemsMenuPrincipal] = useState('');
    const [itemUrl, setitemsUrl] = useState('');



    /*
    Contexto para implementar las tablas EGRESADOS,MATRICULADOS,ADMITIDOS, E INSCRITOS
    */


    const itemsMenuCallBack = useCallback((e) => {
        let itemsurls = e?.lastIndexOf('estadisticas_');

        let n = e?.lastIndexOf('/');
        let totalcletras = e?.length;
        const items_sub = e?.substring(0, Number(n)).replace('/estadisticas_programas/', '').replace('/', '');
        const items = e
            ?.substring(Number(n), Number(totalcletras))
            .replace('/estadisticas_programas/', '')
            .replace('/', '');

        if (itemsurls === 1) {
            const str1 = e?.replace('estadisticas_programas', 'nivel_uno').replace('estadisticas_fecha', 'nivel_dos');
            setitemsUrl(str1?.substring(1, 10));
        }
        //
        if (items_sub) {
            setitemsMenuPrincipal(items_sub);
            // eslint-disable-next-line no-undef
        }
        if (items) {
            setitemsMenu(items);
        }

        // eslint-disable-next-line no-use-before-define
    }, []);
    const clearALL = useCallback((select) => {
        // eslint-disable-next-line no-lone-blocks
        {
            (() => {
                switch (select) {
                    case 'graduados':
                        return (<>{setStateGraduados(VALUE_INIT_PROGRAMAS[0])}</>)
                    case 'matriculados':
                        return (<>{setStateMatriculados(VALUE_INIT_MATRICULADOS[0])}</>)
                    case 'inscritos':
                        return (<>{setStateInscritos(VALUE_INIT_MATRICULADOS[0])}</>)
                    case 'admitidos':
                        return (<>{setStateAdmitidos(VALUE_INIT_MATRICULADOS[0])}</>);
                    case 'por_fecha':
                          return (<>{setStatePorFecha(VALUE_INIT_MATRICULADOS[0])}</>);
                        default:
                        return (<>{sessionStorage.setItem('ITEM_SELECT',JSON.stringify({memorizer:'Graduados'}))}</>);
                }
            })();
        }
    }, []);

    const consultaGraduadosData = useCallback((resp) => {
        const objet = [
            {
                Items: resp?.items ? resp?.items : [],
                Chart: resp?.chart ? resp?.chart : [],
                DonutData: resp?.donutData ? resp?.donutData : [],
                Totales: resp?.general ? resp?.general : [],
                InscritosAdmitidosMatriculados: resp?.InscritosAdmitidosMatriculados? resp?.InscritosAdmitidosMatriculados:[],
                ApexBarChartData: resp?.apexBarChartData ? resp?.apexBarChartData : [],
                Piechart: resp?.piechart ? resp?.piechart : [],
                EstadistInscritos: resp?.EstadistInscritos ? resp?.EstadistInscritos : [],
                EstadistAdmitidos: resp?.EstadistAdmitidos ? resp?.EstadistAdmitidos : [],
            },
        ];
        setStateGraduados(objet[0]);
        let progInfo = JSON.parse(sessionStorage.getItem('ITEM_PROGRAM'));
        setInfo(progInfo);
    }, []);
    const consultaMatriculadosData = useCallback((resp) => {
        const objet = [
            {
                Items: resp?.items ? resp?.items : [],
                Genero: resp?.genero ? resp?.genero : [],
                Estracto: resp?.estracto ? resp?.estracto : [],
                Categorias: resp?.categoria ? resp?.categoria : [],
                Chart: resp?.chart ? resp?.chart : [],
                Donut: resp?.donut ? resp?.donut : [],
                ApexBarChartGenero: resp?.apexBarChartGenero ? resp?.apexBarChartGenero : [],
                ApexBarChartEstracto: resp?.apexBarChartEstracto ? resp?.apexBarChartEstracto : [],
            },
        ];
        setStateMatriculados(objet[0]);
        let progInfo = JSON.parse(sessionStorage.getItem('ITEM_PROGRAM'));
        setInfo(progInfo);
    }, []);

    const consultaAdmitidosData = useCallback((resp) => {
        const objet = [
            {
                Items: resp?.items ? resp?.items : [],
            },
        ];
        setStateAdmitidos(objet[0]);
        let progInfo = JSON.parse(sessionStorage.getItem('ITEM_PROGRAM'));
        setInfo(progInfo);
    }, []);


    const consultaInscritosData = useCallback((resp) => {
        const objet = [
            {
                Items: resp?.items ? resp?.items : [],
                ChartUno: resp?.apexBarChartData1 ? resp?.apexBarChartData1 : [],
                ChartDos: resp?.apexBarChartData2 ? resp?.apexBarChartData2 : [],
                labels1: resp?.labels1 ? resp?.labels1 : [],
                labels2: resp?.labels2 ? resp?.labels2 : [],
            },
        ];
        setStateInscritos(objet[0]);
        let progInfo = JSON.parse(sessionStorage.getItem('ITEM_PROGRAM'));
        setInfo(progInfo);
    }, []);

    const consultaPorFechaData = useCallback((resp) => {
        const objet = [
            {
              Items: resp?.items ? resp?.items : [],
              Piechart: resp?.piechart ? resp?.piechart : [],
            },
        ];
        setStatePorFecha(objet[0]);
        let progInfo = JSON.parse(sessionStorage.getItem('ITEM_PROGRAM'));
        setInfo(progInfo);
    }, []);


    const importarDataCvs = useCallback((dataFiles) => {
        /*
let arrayObj=[];

// eslint-disable-next-line array-callback-return
const objet = Object.keys(dataFiles)?.map((key) => {
  arrayObj.push({
    filename: dataFiles[key]?.filename,
    size: dataFiles[key]?.size,
    formattedSize: dataFiles[key]?.formattedSize,
    lastModified: dataFiles[key]?.lastModified,
    type: dataFiles[key]?.type,
  });
});
*/
        console.log(dataFiles);

        //const urla = `accion=importar&opcion=registrar`;
        //const respuesta = api.importFile(`${urla}`, dataFiles);
        //respuesta.then(function (resp) {
        //console.log(resp)
        //});
    }, []);

    const consultData = useCallback(
      (resp, itemUrl) => {
          // eslint-disable-next-line no-lone-blocks
          {
              (() => {
                  switch (itemUrl) {
                      case 'graduados':
                          return consultaGraduadosData(resp);
                      case 'matriculados':
                          return consultaMatriculadosData(resp);
                      case 'inscritos':
                          return consultaInscritosData(resp);
                      case 'admitidos':
                          return consultaAdmitidosData(resp);
                      case 'por_fecha':
                          return consultaPorFechaData(resp);
                      default:
                          return <>{''}</>;
                  }
              })();
          }
      },
      [consultaAdmitidosData, consultaGraduadosData, consultaInscritosData, consultaMatriculadosData, consultaPorFechaData]
    );
    const data = {
        categoryGraduados,
        categoryMatriculados,
        categoryAdmitidos,
        itemsMenuCallBack,
        categoryInscritos,
        categoryPorFecha,
        isLoading,
        setLoading,
        consultaGraduadosData,
        jsonDataImport,
        JsonDataSetter,
        selectedFile,
        setSelectedFile,
        itemsmenu,
        itemsmenuprincipal,
        itemUrl,
        consultaMatriculadosData,
        consultaAdmitidosData,
        consultaInscritosData,
        importarDataCvs,
        consultData,
        info,
        setInfo,
        clearALL,
    };
    // eslint-disable-next-line react/jsx-no-undef
    return (
        <>
            <DashboardContext.Provider value={data}>{children}</DashboardContext.Provider>
        </>
    );
};
export { DashboardContext, DashboardProvider };
